import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import WebApp from '@twa-dev/sdk';
import { ThemeProvider } from 'styled-components';
import GlobalStyles from './styles/globalStyles';
import theme from './styles/theme';
import { I18nextProvider } from 'react-i18next';
import i18n from './i18n';
import './i18n';
import HomePage from './pages/HomePage';
import CreateHabitPage from './pages/CreateHabitPage';
import EditHabitPage from './pages/EditHabitPage';
import StatisticsPage from './pages/StatisticsPage';
import SettingsPage from './pages/SettingsPage';
import './App.css';
import api from './api.js';

const App = () => {
  const [currentTheme, setCurrentTheme] = useState('light');

  // Инициализация Telegram Mini App
  useEffect(() => {
    // Проверяем, находимся ли мы внутри Telegram Web App
    const isInTelegramWebApp = window.Telegram && window.Telegram.WebApp;
    
    if (isInTelegramWebApp) {
      // Инициализируем Telegram WebApp
      WebApp.ready();
      
      // Устанавливаем настройки отображения
      WebApp.expand();
      
      // Определяем тему на основе темы Telegram
      const isDarkTheme = WebApp.colorScheme === 'dark';
      setCurrentTheme(isDarkTheme ? 'dark' : 'light');
      document.body.setAttribute('data-theme', isDarkTheme ? 'dark' : 'light');
      
      // Переопределяем основные цвета из темы Telegram
      if (WebApp.themeParams.button_color) {
        document.documentElement.style.setProperty(
          '--button-color', 
          WebApp.themeParams.button_color
        );
      }
    } else {
      // Не в Telegram - используем системные предпочтения или светлую тему по умолчанию
      const prefersDark = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
      setCurrentTheme(prefersDark ? 'dark' : 'light');
      document.body.setAttribute('data-theme', prefersDark ? 'dark' : 'light');
      
      // Слушаем изменения системной темы
      window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', event => {
        const newTheme = event.matches ? 'dark' : 'light';
        setCurrentTheme(newTheme);
        document.body.setAttribute('data-theme', newTheme);
      });
    }
    
    // Устанавливаем фактическую высоту для мобильных устройств
    const setAppHeight = () => {
      const vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--app-height', `${window.innerHeight}px`);
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    };
    
    setAppHeight();
    window.addEventListener('resize', setAppHeight);
    
    return () => {
      window.removeEventListener('resize', setAppHeight);
    };
  }, []);

  return (
    <I18nextProvider i18n={i18n}>
      <ThemeProvider theme={theme}>
        <GlobalStyles />
        <Router>
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/create" element={<CreateHabitPage />} />
            <Route path="/edit/:id" element={<EditHabitPage />} />
            <Route path="/statistics" element={<StatisticsPage />} />
            <Route path="/settings" element={<SettingsPage />} />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </Router>
      </ThemeProvider>
    </I18nextProvider>
  );
};

export default App; 