import React, { useEffect, useState } from 'react';
import habitApi from '../services/HabitApi';

const RecommendationsModal = ({ isOpen, onClose, habits }) => {
  const [recommendations, setRecommendations] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  useEffect(() => {
    if (isOpen) {
      fetchRecommendations();
    }
  }, [isOpen]);
  
  const fetchRecommendations = async () => {
    try {
      setLoading(true);
      setError(null);
      
      // Получаем рекомендации от API или генерируем их локально
      const data = await habitApi.getRecommendations();
      setRecommendations(data || []);
    } catch (error) {
      console.error('Ошибка при получении рекомендаций:', error);
      setError('Не удалось загрузить рекомендации');
    } finally {
      setLoading(false);
    }
  };

  // Если модальное окно не открыто, не рендерим содержимое
  if (!isOpen) return null;

  // Стили для модального окна
  const modalOverlayStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 1000
  };
  
  const modalContentStyle = {
    position: 'relative',
    backgroundColor: 'white',
    borderRadius: '8px',
    padding: '20px',
    maxWidth: '500px',
    width: '90%',
    maxHeight: '80vh',
    overflow: 'auto',
    boxShadow: '0 4px 10px rgba(0, 0, 0, 0.2)'
  };
  
  const closeButtonStyle = {
    position: 'absolute',
    top: '15px',
    right: '15px',
    background: 'none',
    border: 'none',
    fontSize: '24px',
    cursor: 'pointer',
    color: '#666'
  };
  
  const headerStyle = {
    marginBottom: '20px',
    fontSize: '20px',
    fontWeight: 'bold',
    color: '#333',
    paddingRight: '30px'
  };
  
  const recommendationCardStyle = {
    border: '1px solid #e0e0e0',
    borderRadius: '6px',
    padding: '15px',
    marginBottom: '15px',
    backgroundColor: '#f9f9f9'
  };
  
  const recommendationTitleStyle = {
    fontSize: '16px',
    fontWeight: '600',
    marginBottom: '8px',
    color: '#444'
  };
  
  const recommendationDescStyle = {
    fontSize: '14px',
    color: '#666',
    marginBottom: '10px'
  };
  
  const relatedHabitsStyle = {
    fontSize: '13px',
    color: '#0077cc'
  };

  // Находим привычки по ID
  const getHabitById = (id) => {
    return habits.find(h => h.id === id);
  };

  return (
    <div style={modalOverlayStyle} onClick={onClose}>
      <div style={modalContentStyle} onClick={e => e.stopPropagation()}>
        <button style={closeButtonStyle} onClick={onClose}>×</button>
        
        <h3 style={headerStyle}>Рекомендации для вас</h3>
        
        {loading ? (
          <div style={{ textAlign: 'center', padding: '20px' }}>
            Загрузка рекомендаций...
          </div>
        ) : error ? (
          <div style={{ color: '#f44336', textAlign: 'center', padding: '20px' }}>
            {error}
          </div>
        ) : recommendations.length === 0 ? (
          <div style={{ textAlign: 'center', padding: '20px' }}>
            У нас пока нет персонализированных рекомендаций для вас.
            <br />
            Продолжайте отслеживать ваши привычки!
          </div>
        ) : (
          recommendations.map((recommendation, index) => (
            <div key={index} style={recommendationCardStyle}>
              <h4 style={recommendationTitleStyle}>{recommendation.title}</h4>
              <p style={recommendationDescStyle}>{recommendation.description}</p>
              
              {recommendation.habits && recommendation.habits.length > 0 && (
                <div style={relatedHabitsStyle}>
                  <strong>Связанные привычки:</strong>
                  <ul>
                    {recommendation.habits.map((habitId) => {
                      const habit = getHabitById(habitId);
                      return habit ? (
                        <li key={habitId}>{habit.title}</li>
                      ) : null;
                    })}
                  </ul>
                </div>
              )}
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default RecommendationsModal; 