import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import MainLayout from '../components/layout/MainLayout';
import ErrorHandler from '../components/ErrorHandler';
import habitApi from '../services/HabitApi';
import theme from '../styles/theme';
import { useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const Content = styled.div`
  padding: ${theme.spacing.md};
  max-width: 800px;
  margin: 0 auto;
  width: 100%;
`;

const LoadingContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  color: ${theme.colors.gray[600]};
`;

const StatsContainer = styled.div`
  margin-bottom: ${theme.spacing.lg};
`;

const StatsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: ${theme.spacing.md};
  margin-bottom: ${theme.spacing.lg};
  
  @media (min-width: 768px) {
    grid-template-columns: repeat(4, 1fr);
  }
`;

const StatCard = styled.div`
  padding: ${theme.spacing.md};
  border-radius: ${theme.borderRadius.md};
  border: 1px solid ${theme.colors.gray[300]};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
`;

const StatValue = styled.div`
  font-size: 32px;
  font-weight: ${theme.typography.fontWeight.bold};
  color: ${theme.colors.primary};
  margin-bottom: ${theme.spacing.xs};
`;

const StatLabel = styled.div`
  font-size: ${theme.typography.fontSize.small};
  color: ${theme.colors.gray[600]};
`;

const SectionTitle = styled.h2`
  font-size: ${theme.typography.fontSize.large};
  font-weight: ${theme.typography.fontWeight.semibold};
  margin-bottom: ${theme.spacing.md};
  color: var(--text-color);
`;

const HabitProgressList = styled.div`
  display: flex;
  flex-direction: column;
  gap: ${theme.spacing.md};
`;

const HabitProgressItem = styled.div`
  padding: ${theme.spacing.md};
  border-radius: ${theme.borderRadius.md};
  border: 1px solid ${theme.colors.gray[300]};
`;

const HabitName = styled.div`
  font-weight: ${theme.typography.fontWeight.medium};
  margin-bottom: ${theme.spacing.xs};
  color: var(--text-color);
`;

const ProgressContainer = styled.div`
  margin-top: ${theme.spacing.sm};
`;

const ProgressBar = styled.div`
  height: 6px;
  background-color: ${theme.colors.gray[300]};
  border-radius: ${theme.borderRadius.full};
  margin-top: ${theme.spacing.xs};
  overflow: hidden;
`;

const Progress = styled.div`
  height: 100%;
  background-color: ${props => {
    if (props.$percentage >= 100) return theme.colors.success;
    if (props.$percentage >= 60) return theme.colors.primary;
    if (props.$percentage >= 30) return theme.colors.warning;
    return theme.colors.error;
  }};
  width: ${props => props.$percentage}%;
  transition: width 0.3s ease;
`;

const ProgressText = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: ${theme.typography.fontSize.small};
  color: ${theme.colors.gray[600]};
`;

const HabitCategory = styled.span`
  font-size: ${theme.typography.fontSize.small};
  background-color: ${theme.colors.gray[200]};
  color: ${theme.colors.gray[700]};
  padding: ${theme.spacing.xxs} ${theme.spacing.xs};
  border-radius: ${theme.borderRadius.full};
  display: inline-block;
  margin-left: ${theme.spacing.xs};
`;

const NoDataMessage = styled.div`
  text-align: center;
  padding: ${theme.spacing.lg} 0;
  color: ${theme.colors.gray[600]};
`;

const StatisticsPage = () => {
  const [habits, setHabits] = useState([]);
  const { t } = useTranslation();
  const [stats, setStats] = useState({
    totalHabits: 0,
    completedToday: 0,
    totalCompleted: 0,
    streakAverage: 0,
    bestStreak: { name: '', value: 0 },
    mostConsistent: { name: '', value: 0 }
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  const location = useLocation();

  useEffect(() => {
    fetchData();
  }, [location]);
  
  const fetchData = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const habitsData = await habitApi.getHabits();
      console.log("Данные о привычках получены:", habitsData);
      setHabits(habitsData);
      
      // Рассчитываем статистику
      calculateStats(habitsData);
    } catch (err) {
      console.error('Ошибка при загрузке данных:', err);
      setError(err);
    } finally {
      setLoading(false);
    }
  };
  
  const calculateStats = (habitData) => {
    console.log("Данные о привычках в calculateStats:", habitData);
    if (!habitData || habitData.length === 0) {
      return;
    }
    
    const today = new Date().toISOString().split('T')[0];
    const completedTodayCount = habitData.filter(habit => 
      habit.completionHistory && habit.completionHistory.includes(today)
    ).length;
    
    // Суммируем все выполнения
    let totalCompletions = 0;
    habitData.forEach(habit => {
      if (habit.completionHistory) {
        totalCompletions += habit.completionHistory.length;
      }
    });
    
    // Находим лучшую серию
    let bestStreakHabit = { name: '', value: 0 };
    habitData.forEach(habit => {
      if ((habit.maxStreak || 0) > bestStreakHabit.value) {
        bestStreakHabit = { 
          name: habit.name,
          value: habit.maxStreak || 0
        };
      }
    });
    
    // Находим наиболее последовательную привычку
    let mostConsistentHabit = { name: '', value: 0 };
    habitData.forEach(habit => {
      const completionRate = habit.completionHistory
        ? (habit.completionHistory.length / (habit.totalDays || 1)) * 100
        : 0;
      
      if (completionRate > mostConsistentHabit.value) {
        mostConsistentHabit = { 
          name: habit.name, 
          value: Math.round(completionRate) 
        };
      }
    });
    
    // Средняя серия
    const streakSum = habitData.reduce((sum, habit) => sum + (habit.streak || 0), 0);
    const streakAvg = habitData.length > 0 ? Math.round(streakSum / habitData.length) : 0;
    
    setStats({
      totalHabits: habitData.length,
      completedToday: completedTodayCount,
      totalCompleted: totalCompletions,
      streakAverage: streakAvg,
      bestStreak: bestStreakHabit,
      mostConsistent: mostConsistentHabit
    });
  };
  
  // Сортируем привычки по прогрессу
  const sortedHabits = [...habits].sort((a, b) => {
    // Убеждаемся, что значения числовые, с дефолтом 0
    const currentA = Number(a.streak) || 0;
    const targetA = Number(a.targetStreak) || 21;
    const currentB = Number(b.streak) || 0;
    const targetB = Number(b.targetStreak) || 21;

    const progressA = targetA > 0 ? (currentA / targetA) * 100 : 0;
    const progressB = targetB > 0 ? (currentB / targetB) * 100 : 0;

    return progressB - progressA;
  });
  
  return (
    <MainLayout title={t('statistics.title')}>
      <Content>
        {error && (
          <ErrorHandler 
            error={error} 
            onRetry={fetchData}
            customMessage={t('statistics.loadingError')}
          />
        )}
        
        {loading ? (
          <LoadingContainer>
            <p>Загрузка статистики...</p>
          </LoadingContainer>
        ) : habits.length === 0 ? (
          <NoDataMessage>
            У вас пока нет привычек для отображения статистики
          </NoDataMessage>
        ) : (
          <>
            <StatsContainer>
              <SectionTitle>{t('statistics.generalTitle')}</SectionTitle>
              <StatsGrid>
                <StatCard>
                  <StatLabel>{t('statistics.totalHabits')}</StatLabel>
                  <StatValue>{stats.totalHabits}</StatValue>
                </StatCard>
                <StatCard>
                  <StatLabel>{t('statistics.completedToday')}</StatLabel>
                  <StatValue>{stats.completedToday}</StatValue>
                </StatCard>
                <StatCard>
                  <StatLabel>{t('statistics.totalCompleted')}</StatLabel>
                  <StatValue>{stats.totalCompleted}</StatValue>
                </StatCard>
                <StatCard>
                  <StatLabel>{t('statistics.averageStreak')}</StatLabel>
                  <StatValue>{stats.streakAverage}</StatValue>
                </StatCard>
              </StatsGrid>
            </StatsContainer>
            
            <SectionTitle>{t('statistics.progressTitle')}</SectionTitle>
            <HabitProgressList>
              {sortedHabits.map(habit => {
                console.log("Рендеринг привычки в прогрессе:", habit);
                const progress = Math.min(
                  // Убеждаемся, что значения числовые, с дефолтом 0
                  Math.round(((Number(habit.streak) || 0) / (Number(habit.targetStreak) || 21)) * 100),
                  100
                );
                
                return (
                  <HabitProgressItem key={habit.id}>
                    <HabitName>{habit.title || habit.name}</HabitName>
                    {habit.category && (
                      <HabitCategory>{habit.category}</HabitCategory>
                    )}
                    {habit.description && (
                      <p>{habit.description}</p>
                    )}
                    
                    <ProgressContainer>
                      <ProgressText>
                        <span>{habit.streak} из {habit.targetStreak || 21} дней</span>
                        <span>{progress}%</span>
                      </ProgressText>
                      <ProgressBar>
                        <Progress $percentage={progress} />
                      </ProgressBar>
                    </ProgressContainer>
                  </HabitProgressItem>
                );
              })}
            </HabitProgressList>
          </>
        )}
      </Content>
    </MainLayout>
  );
};

export default StatisticsPage; 