import apiService from './ApiService';
import localforage from 'localforage';

class HabitApi {
  constructor() {
    // Инициализация хранилища для привычек
    localforage.config({
      name: 'habitTracker',
      storeName: 'habits'
    });
  }

  // Получение списка всех привычек
  async getHabits() {
    try {
      // Проверяем, есть ли подключение к сети
      if (!navigator.onLine) {
        console.log('Отсутствует подключение к интернету, использую локальное хранилище');
        return await this.getLocalHabits();
      }
      
      console.log('Онлайн. Пытаюсь загрузить привычки с сервера.');
      const habits = await apiService.get('/habits');
      console.log('Ответ сервера по привычкам:', habits);
      
      // Если сервер вернул данные, сохраняем их в локальное хранилище
      if (habits && habits.length > 0) {
        await localforage.setItem('habits', habits);
        console.log('Привычки с сервера сохранены в локальное хранилище.');
        return habits;
      } else {
         console.log('Сервер вернул пустой список привычек. Использую локальное хранилище.');
         return await this.getLocalHabits();
      }
    } catch (error) {
      console.error('Ошибка при получении привычек с сервера:', error);
      // Если запрос не удался, пытаемся получить данные из локального хранилища
      console.log('Ошибка при получении с сервера. Использую локальное хранилище.');
      return await this.getLocalHabits();
    }
  }

  // Получение привычек из локального хранилища
  async getLocalHabits() {
    try {
      let localHabits = await localforage.getItem('habits');
      console.log("Данные из локального хранилища (getLocalHabits):", localHabits); // Временное логирование
      if (!localHabits) {
        localHabits = [];
        await localforage.setItem('habits', localHabits);
      }
      return localHabits;
    } catch (error) {
      console.error('Ошибка при получении привычек из локального хранилища:', error);
      return [];
    }
  }

  // Получение одной привычки по ID
  async getHabitById(id) {
    try {
      if (!navigator.onLine) {
        const localHabits = await this.getLocalHabits();
        return localHabits.find(habit => habit.id === id);
      }

      const habit = await apiService.get(`/habits/${id}`);
      return habit;
    } catch (error) {
      console.error(`Ошибка при получении привычки ${id}:`, error);
      const localHabits = await this.getLocalHabits();
      return localHabits.find(habit => habit.id === id);
    }
  }

  // Создание новой привычки
  async createHabit(habitData) {
    try {
      const habitToSend = {
        ...habitData,
        title: habitData.name,
        createdAt: new Date().toISOString(),
        streak: 0,
        maxStreak: 0,
        completionHistory: [],
        targetStreak: habitData.targetStreak || 21
      };
      delete habitToSend.name;

      if (!navigator.onLine) {
        return await this.createLocalHabit(habitToSend);
      }

      // Отправляем запрос на сервер
      const response = await apiService.post('/habits', habitToSend);
      const { id } = response; // Получаем ID из ответа сервера
      
      // Создаем полный объект привычки для локального хранения
      const newHabit = {
        ...habitToSend,
        id: id, // Используем ID, полученный от сервера
        // Убедимся, что все необходимые поля присутствуют, возможно, сервер что-то не возвращает
        completionHistory: habitToSend.completionHistory || [],
      };

      // Обновляем локальное хранилище
      const localHabits = await this.getLocalHabits();
      localHabits.push(newHabit); // Добавляем созданный на клиенте объект
      await localforage.setItem('habits', localHabits);
      
      // Добавляем лог после сохранения в локальное хранилище
      const habitsAfterSave = await this.getLocalHabits();
      console.log("Данные в локальном хранилище сразу после сохранения (createHabit):", habitsAfterSave);

      return newHabit; // Возвращаем созданный на клиенте объект
    } catch (error) {
      console.error('Ошибка при создании привычки:', error);
      // Если запрос к серверу не удался, сохраняем в локальное хранилище как офлайн-изменение
      return await this.createLocalHabit(habitData);
    }
  }

  // Создание привычки в локальном хранилище
  async createLocalHabit(habitData) {
    try {
      const localHabits = await this.getLocalHabits();
      const newHabit = {
        ...habitData,
        id: `local_${Date.now()}`,
        createdAt: new Date().toISOString(),
        streak: 0,
        maxStreak: 0,
        completionHistory: [],
        targetStreak: habitData.targetStreak || 21
      };
      
      localHabits.push(newHabit);
      await localforage.setItem('habits', localHabits);
      
      return newHabit;
    } catch (error) {
      console.error('Ошибка при создании привычки в локальном хранилище:', error);
      throw error;
    }
  }

  // Обновление привычки
  async updateHabit(id, habitData) {
    try {
      const habitToSend = {
        ...habitData,
        title: habitData.name,
        updatedAt: new Date().toISOString()
      };
      delete habitToSend.name;

      if (!navigator.onLine) {
        return await this.updateLocalHabit(id, habitToSend);
      }

      const updatedHabit = await apiService.put(`/habits/${id}`, habitToSend);
      
      // Обновляем локальное хранилище
      const localHabits = await this.getLocalHabits();
      const habitIndex = localHabits.findIndex(habit => habit.id === id);
      
      if (habitIndex !== -1) {
        localHabits[habitIndex] = updatedHabit;
        await localforage.setItem('habits', localHabits);
      }
      
      return updatedHabit;
    } catch (error) {
      console.error(`Ошибка при обновлении привычки ${id}:`, error);
      return await this.updateLocalHabit(id, habitData);
    }
  }

  // Обновление привычки в локальном хранилище
  async updateLocalHabit(id, habitData) {
    try {
      const localHabits = await this.getLocalHabits();
      const habitIndex = localHabits.findIndex(habit => habit.id === id);
      
      if (habitIndex !== -1) {
        localHabits[habitIndex] = {
          ...localHabits[habitIndex],
          ...habitData,
          updatedAt: new Date().toISOString()
        };
        
        await localforage.setItem('habits', localHabits);
        return localHabits[habitIndex];
      }
      
      throw new Error('Привычка не найдена');
    } catch (error) {
      console.error('Ошибка при обновлении привычки в локальном хранилище:', error);
      throw error;
    }
  }

  // Удаление привычки
  async deleteHabit(id) {
    try {
      if (!navigator.onLine) {
        return await this.deleteLocalHabit(id);
      }

      await apiService.delete(`/habits/${id}`);
      
      // Удаляем из локального хранилища
      const localHabits = await this.getLocalHabits();
      const filteredHabits = localHabits.filter(habit => habit.id !== id);
      await localforage.setItem('habits', filteredHabits);
      
      return { success: true };
    } catch (error) {
      console.error(`Ошибка при удалении привычки ${id}:`, error);
      return await this.deleteLocalHabit(id);
    }
  }

  // Удаление привычки из локального хранилища
  async deleteLocalHabit(id) {
    try {
      const localHabits = await this.getLocalHabits();
      const filteredHabits = localHabits.filter(habit => habit.id !== id);
      await localforage.setItem('habits', filteredHabits);
      return { success: true };
    } catch (error) {
      console.error('Ошибка при удалении привычки из локального хранилища:', error);
      throw error;
    }
  }

  // Отметка привычки как завершенной сегодня
  async completeHabit(id) {
    try {
      if (!navigator.onLine) {
        return await this.completeLocalHabit(id);
      }

      // Отправляем запрос на сервер для отметки завершения
      await apiService.post(`/habits/${id}/complete`);
      
      // Получаем актуальные данные привычки с сервера
      const updatedHabit = await apiService.get(`/habits/${id}`);
      console.log("Обновленные данные привычки:", updatedHabit);

      // Проверяем, что получили валидные данные
      if (!updatedHabit || !updatedHabit.id) {
        throw new Error('Не удалось получить обновленные данные привычки');
      }

      // Обновляем локальное хранилище
      const localHabits = await this.getLocalHabits();
      const habitIndex = localHabits.findIndex(habit => habit.id === id);
      
      if (habitIndex !== -1) {
        localHabits[habitIndex] = updatedHabit;
        await localforage.setItem('habits', localHabits);
      }

      return updatedHabit;
    } catch (error) {
      console.error(`Ошибка при отметке привычки ${id} как выполненной:`, error);
      // Если запрос к серверу не удался, пытаемся обновить локально
      return await this.completeLocalHabit(id);
    }
  }

  // Отметка привычки как завершенной в локальном хранилище
  async completeLocalHabit(id) {
    try {
      const localHabits = await this.getLocalHabits();
      const habitIndex = localHabits.findIndex(habit => habit.id === id);
      
      if (habitIndex !== -1) {
        const today = new Date().toISOString().split('T')[0];
        const habit = localHabits[habitIndex];
        
        // Проверяем, не была ли привычка уже отмечена сегодня
        const alreadyCompleted = habit.completionHistory && 
          habit.completionHistory.some(date => date.startsWith(today));
        
        if (!alreadyCompleted) {
          // Обновляем историю завершений
          const completionHistory = [...(habit.completionHistory || []), today];
          
          // Обновляем текущую серию
          let streak = habit.streak || 0;
          streak += 1;
          
          // Обновляем максимальную серию если нужно
          const maxStreak = Math.max(streak, habit.maxStreak || 0);
          
          // Обновляем привычку
          localHabits[habitIndex] = {
            ...habit,
            lastCompleted: today,
            completionHistory,
            streak,
            maxStreak
          };
          
          await localforage.setItem('habits', localHabits);
          return localHabits[habitIndex];
        }
        
        return habit; // Возвращаем существующую привычку, если она уже завершена сегодня
      }
      
      throw new Error('Привычка не найдена');
    } catch (error) {
      console.error('Ошибка при отметке привычки как выполненной в локальном хранилище:', error);
      throw error;
    }
  }

  // Получение рекомендаций на основе истории привычек
  async getRecommendations() {
    try {
      const recommendations = await apiService.get('/habits/recommendations');
      return recommendations;
    } catch (error) {
      // Если сервер недоступен, генерируем базовые рекомендации
      if (!navigator.onLine || error.message === 'Network Error') {
        const localHabits = await localforage.getItem('habits') || [];
        
        // Простые рекомендации, основанные на локальных данных
        const recommendations = [];
        
        // Находим привычки с прерванными сериями
        const brokenStreaks = localHabits.filter(habit => 
          habit.streak === 0 && habit.maxStreak > 0);
        
        if (brokenStreaks.length > 0) {
          recommendations.push({
            title: 'Восстановите свои привычки',
            description: 'У вас есть привычки с прерванными сериями. Попробуйте возобновить их!',
            habits: brokenStreaks.map(h => h.id)
          });
        }
        
        // Находим привычки с длинными сериями
        const longStreaks = localHabits.filter(habit => habit.streak >= 7);
        
        if (longStreaks.length > 0) {
          recommendations.push({
            title: 'Отлично справляетесь!',
            description: 'Продолжайте поддерживать эти привычки для достижения долгосрочных результатов.',
            habits: longStreaks.map(h => h.id)
          });
        }
        
        return recommendations;
      }
      throw error;
    }
  }

  // Синхронизация локальных данных с сервером
  async syncWithServer() {
    if (!navigator.onLine) {
      console.log('Нет подключения к интернету, синхронизация отложена');
      return false;
    }
    
    try {
      // Получаем все локальные привычки
      const localHabits = await localforage.getItem('habits') || [];
      
      // Отправляем на сервер только привычки с локальными ID
      for (const habit of localHabits) {
        if (habit.id.toString().startsWith('local_')) {
          // Создаем новую привычку на сервере
          const { id, ...habitData } = habit;
          await apiService.post('/habits', habitData);
        }
      }
      
      // Получаем обновленный список с сервера
      const serverHabits = await apiService.get('/habits');
      
      // Обновляем локальное хранилище
      await localforage.setItem('habits', serverHabits);
      
      console.log('Синхронизация с сервером успешно завершена');
      return true;
    } catch (error) {
      console.error('Ошибка при синхронизации с сервером:', error);
      return false;
    }
  }
}

const habitApi = new HabitApi();
export default habitApi; 