import axios from 'axios';
import localforage from 'localforage';

// Инициализация хранилища для кэширования запросов
localforage.config({
  name: 'habitTracker',
  storeName: 'apiRequests'
});

class ApiService {
  constructor() {
    // Используем относительный путь для прокси
    this.baseURL = '/api';
    this.axiosInstance = axios.create({
      baseURL: this.baseURL,
      timeout: 15000,
      headers: {
        'Content-Type': 'application/json'
      }
    });

    // Добавляем перехватчик для логирования ошибок
    this.axiosInstance.interceptors.response.use(
      response => response,
      error => {
        console.error('Ошибка API:', {
          url: error.config?.url,
          method: error.config?.method,
          status: error.response?.status,
          message: error.message,
          data: error.response?.data
        });
        return Promise.reject(error);
      }
    );

    // Добавляем обработчик для офлайн-режима
    window.addEventListener('online', this.handleOnline.bind(this));
    window.addEventListener('offline', this.handleOffline.bind(this));
  }

  // Обработчик перехода в онлайн
  async handleOnline() {
    console.log('Восстановлено подключение к интернету');
    try {
      await this.syncPendingChanges();
    } catch (error) {
      console.error('Ошибка при синхронизации после восстановления соединения:', error);
    }
  }

  // Обработчик перехода в офлайн
  handleOffline() {
    console.log('Потеряно подключение к интернету');
  }

  // Метод для синхронизации отложенных изменений
  async syncPendingChanges() {
    try {
      const pendingChanges = await localforage.getItem('pendingChanges') || [];
      if (pendingChanges.length === 0) return;

      console.log('Начинаю синхронизацию отложенных изменений...');
      
      for (const change of pendingChanges) {
        try {
          switch (change.type) {
            case 'create':
              await this.axiosInstance.post(change.url, change.data);
              break;
            case 'update':
              await this.axiosInstance.put(change.url, change.data);
              break;
            case 'delete':
              await this.axiosInstance.delete(change.url);
              break;
          }
        } catch (error) {
          console.error(`Ошибка при синхронизации изменения ${change.type}:`, error);
          // Продолжаем с следующим изменением
          continue;
        }
      }

      // Очищаем отложенные изменения после успешной синхронизации
      await localforage.setItem('pendingChanges', []);
      console.log('Синхронизация отложенных изменений завершена');
    } catch (error) {
      console.error('Ошибка при синхронизации отложенных изменений:', error);
      throw error;
    }
  }

  // Метод для сохранения изменений в локальное хранилище
  async saveLocalChanges(method, url, data) {
    try {
      const pendingChanges = await localforage.getItem('pendingChanges') || [];
      pendingChanges.push({
        type: method.toLowerCase(),
        url,
        data,
        timestamp: Date.now()
      });
      await localforage.setItem('pendingChanges', pendingChanges);
    } catch (error) {
      console.error('Ошибка при сохранении локальных изменений:', error);
    }
  }

  // Метод для обработки запросов в офлайн-режиме
  async handleOfflineRequest(config) {
    const key = `${config.method}-${config.url}-${JSON.stringify(config.data || {})}`;
    
    try {
      // Проверяем, есть ли кэшированные данные для этого запроса
      const cachedData = await localforage.getItem(key);
      
      // Для GET запросов
      if (config.method === 'get') {
        if (cachedData) {
          return { data: cachedData, status: 200, statusText: 'OK (cached)' };
        }
        
        // Для GET /habits возвращаем пустой массив, если нет кэшированных данных
        if (config.url === '/habits' || config.url.endsWith('/habits')) {
          return { data: [], status: 200, statusText: 'OK (empty cached)' };
        }
        
        throw new Error('No cached data available and no network connection');
      } 
      
      // Для POST, PUT и DELETE запросов
      const tempData = typeof config.data === 'string' ? JSON.parse(config.data) : Object.assign({}, config.data);
      
      if (config.method === 'post' && !tempData.id) {
        tempData.id = `local_${Date.now()}`;
      }
      
      await this.saveLocalChanges(config.method, config.url, tempData);
      return { data: tempData, status: 200, statusText: 'OK (offline)', isOffline: true };
    } catch (error) {
      console.error('Ошибка при обработке офлайн-запроса:', error);
      throw error;
    }
  }

  // Методы для работы с API
  async get(url) {
    try {
      const response = await this.axiosInstance.get(url);
      await localforage.setItem(`${url}`, response.data);
      return response.data;
    } catch (error) {
      if (!navigator.onLine) {
        return this.handleOfflineRequest({ method: 'get', url });
      }
      throw error;
    }
  }

  async post(url, data) {
    try {
      const response = await this.axiosInstance.post(url, data);
      return response.data;
    } catch (error) {
      if (!navigator.onLine) {
        return this.handleOfflineRequest({ method: 'post', url, data });
      }
      throw error;
    }
  }

  async put(url, data) {
    try {
      const response = await this.axiosInstance.put(url, data);
      return response.data;
    } catch (error) {
      if (!navigator.onLine) {
        return this.handleOfflineRequest({ method: 'put', url, data });
      }
      throw error;
    }
  }

  async delete(url) {
    try {
      const response = await this.axiosInstance.delete(url);
      return response.data;
    } catch (error) {
      if (!navigator.onLine) {
        return this.handleOfflineRequest({ method: 'delete', url });
      }
      throw error;
    }
  }
}

const apiService = new ApiService();
export default apiService; 