import React, { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import MainLayout from '../components/layout/MainLayout';
import HabitForm from '../components/HabitForm';
import ErrorHandler from '../components/ErrorHandler';
import Button from '../components/ui/Button';
import habitApi from '../services/HabitApi';
import theme from '../styles/theme';
import { useTranslation } from 'react-i18next';

const Content = styled.div`
  padding: ${theme.spacing.md};
  max-width: 600px;
  margin: 0 auto;
  width: 100%;
`;

const LoadingIndicator = styled.div`
  text-align: center;
  padding: ${theme.spacing.md};
  margin-top: ${theme.spacing.lg};
  background-color: rgba(10, 132, 255, 0.1);
  border-radius: ${theme.borderRadius.md};
  color: ${theme.colors.primary};
`;

const OfflineIndicator = styled.div`
  text-align: center;
  padding: ${theme.spacing.md};
  margin-top: ${theme.spacing.lg};
  background-color: rgba(255, 204, 0, 0.1);
  border-radius: ${theme.borderRadius.md};
  color: ${theme.colors.warning};
`;

const CreateHabitPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [offlineMode, setOfflineMode] = useState(false);
  const { t } = useTranslation();
  
  // Проверяем, есть ли рекомендация в state из навигации
  const recommendedHabit = location.state?.recommendation;

  const handleSubmit = async (habitData) => {
    try {
      setIsLoading(true);
      setError(null);
      
      // Проверяем состояние сети
      if (!navigator.onLine) {
        setOfflineMode(true);
      }
      
      await habitApi.createHabit(habitData);
      
      // После успешного создания перенаправляем на главную страницу
      navigate('/');
    } catch (err) {
      console.error('Ошибка при создании привычки:', err);
      
      // Проверяем, можем ли мы создать привычку в офлайн-режиме
      if (!navigator.onLine || err.message.includes('network connection')) {
        try {
          setOfflineMode(true);
          await habitApi.createHabit(habitData);
          navigate('/');
          return;
        } catch (offlineError) {
          console.error('Не удалось создать привычку даже в офлайн-режиме:', offlineError);
          setError(offlineError);
        }
      } else {
        setError(err);
      }
      
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    navigate('/');
  };
  
  // Кнопка действия для заголовка - отмена создания
  const headerActions = (
    <Button 
      variant="text"
      size="small"
      onClick={handleCancel}
    >
      {t('createHabit.cancelButton')}
    </Button>
  );

  return (
    <MainLayout
      title={t('createHabit.title')}
      headerActions={headerActions}
      onBack={() => navigate('/')}
    >
      <Content>
        {!navigator.onLine && (
          <OfflineIndicator>
            {t('createHabit.offlineWarning')}
          </OfflineIndicator>
        )}
        
        {error && (
          <ErrorHandler 
            error={error} 
            onRetry={() => setError(null)}
            customMessage={t('createHabit.createError')}
          />
        )}
        
        <HabitForm 
          onSubmit={handleSubmit} 
          onCancel={handleCancel}
          isDisabled={isLoading}
          initialValues={recommendedHabit || {}}
        />
        
        {isLoading && (
          <LoadingIndicator>
            {t('createHabit.loadingMessage')}
          </LoadingIndicator>
        )}
        
        {offlineMode && !isLoading && (
          <OfflineIndicator>
            {t('createHabit.offlineSuccess')}
          </OfflineIndicator>
        )}
      </Content>
    </MainLayout>
  );
};

export default CreateHabitPage; 