import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import Backend from 'i18next-http-backend';

// Убираем статические импорты файлов переводов
// import translationEN from './locales/en/translation.json';
// import translationRU from './locales/ru/translation.json';

// Ресурсы будут загружаться с помощью Backend
const resources = {};

i18n
  .use(Backend)
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    // resources, // Ресурсы будут загружены через Backend
    fallbackLng: 'ru',
    supportedLngs: ['ru', 'en'],
    debug: process.env.NODE_ENV === 'development',
    interpolation: {
      escapeValue: false,
    },
    detection: {
      order: ['localStorage', 'navigator'],
      caches: ['localStorage'],
      lookupLocalStorage: 'i18nextLng',
    },
    backend: {
      // Путь к файлам переводов относительно папки 'public'
      loadPath: '/locales/{{lng}}/translation.json',
    },
    react: {
      useSuspense: false,
    }
  });

// Добавляем обработчик изменения языка
i18n.on('languageChanged', (lng) => {
  localStorage.setItem('i18nextLng', lng);
});

export default i18n; 