import React from 'react';
import styled from 'styled-components';
import theme from '../styles/theme';
import { useNavigate } from 'react-router-dom';

const CardContainer = styled.div`
  border: 1px solid ${theme.colors.gray[300]};
  border-radius: ${theme.borderRadius.md};
  padding: ${theme.spacing.md};
  margin-bottom: ${theme.spacing.md};
  background-color: transparent;
  transition: transform ${theme.transitions.fast}, border-color ${theme.transitions.fast};
  cursor: pointer;
  position: relative;
  
  &:hover {
    border-color: ${theme.colors.primary};
  }
  
  &:active {
    transform: scale(0.99);
  }
`;

const HabitTitle = styled.h3`
  font-size: ${theme.typography.fontSize.large};
  font-weight: ${theme.typography.fontWeight.semibold};
  margin-bottom: ${theme.spacing.xs};
  margin-right: ${theme.spacing.xl};
  color: var(--text-color);
`;

const HabitDescription = styled.p`
  font-size: ${theme.typography.fontSize.base};
  color: ${theme.colors.gray[700]};
  margin-bottom: ${theme.spacing.sm};
  line-height: ${theme.typography.lineHeight.base};
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
`;

const HabitMeta = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: ${theme.spacing.sm};
  font-size: ${theme.typography.fontSize.small};
`;

const HabitCategory = styled.span`
  color: ${theme.colors.gray[600]};
  display: flex;
  align-items: center;
  gap: ${theme.spacing.xs};
`;

const StreakContainer = styled.div`
  display: flex;
  align-items: center;
  gap: ${theme.spacing.xs};
`;

const StreakBadge = styled.span`
  background-color: ${props => props.$completed ? theme.colors.success : theme.colors.gray[300]};
  color: ${props => props.$completed ? 'white' : theme.colors.gray[700]};
  padding: ${theme.spacing.xxs} ${theme.spacing.xs};
  border-radius: ${theme.borderRadius.full};
  font-weight: ${theme.typography.fontWeight.medium};
  font-size: ${theme.typography.fontSize.small};
  line-height: 1;
`;

const ProgressBar = styled.div`
  height: 4px;
  background-color: ${theme.colors.gray[300]};
  border-radius: ${theme.borderRadius.full};
  margin-top: ${theme.spacing.md};
  overflow: hidden;
`;

const Progress = styled.div`
  height: 100%;
  background-color: ${theme.colors.primary};
  width: ${props => props.$percentage}%;
  transition: width 0.3s ease;
`;

const ActionButtons = styled.div`
  position: absolute;
  top: ${theme.spacing.md};
  right: ${theme.spacing.md};
  display: flex;
  gap: ${theme.spacing.xs};
`;

const ActionButton = styled.button`
  width: 32px;
  height: 32px;
  border-radius: ${theme.borderRadius.full};
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  color: ${theme.colors.gray[700]};
  border: 1px solid ${theme.colors.gray[300]};
  transition: all ${theme.transitions.fast};
  padding: 0;
  
  &:hover {
    background-color: ${theme.colors.gray[200]};
    color: ${theme.colors.primary};
    border-color: ${theme.colors.gray[400]};
  }
`;

const CheckButton = styled(ActionButton)`
  background-color: ${props => props.$completed ? theme.colors.success : 'transparent'};
  color: ${props => props.$completed ? 'white' : theme.colors.gray[700]};
  border-color: ${props => props.$completed ? theme.colors.success : theme.colors.gray[300]};
  
  &:hover {
    background-color: ${props => props.$completed ? theme.colors.success : theme.colors.gray[200]};
    opacity: ${props => props.$completed ? 0.9 : 1};
  }

  svg {
    stroke: ${props => props.$completed ? 'white' : 'currentColor'};
  }
`;

const HabitCard = ({ habit, onComplete, onDelete, onEdit }) => {
  const navigate = useNavigate();
  
  // Рассчитываем процент выполнения привычки
  const current = Number(habit.streak) || 0;
  const target = Number(habit.targetStreak) || 0;
  const completionPercentage = target > 0 ? (current / target) * 100 : 0;
  const cappedPercentage = Math.min(completionPercentage, 100);
  
  // Проверяем, выполнена ли привычка сегодня
  const today = new Date().toISOString().split('T')[0];
  const isCompletedToday = (habit.completionHistory || habit.completedDates || []).includes(today);
  
  // Название и описание привычки с поддержкой старых и новых полей
  const habitTitle = habit.title || habit.name || 'Без названия';
  const habitDescription = habit.description || '';
  
  const category = habit.category || 'Без категории';
  const targetStreak = target;
  
  // Обработчики событий
  const handleComplete = (e) => {
    e.stopPropagation();
    onComplete(habit.id);
  };
  
  const handleEdit = (e) => {
    e.stopPropagation();
    onEdit(habit.id);
  };
  
  const handleDelete = (e) => {
    e.stopPropagation();
    onDelete(habit.id);
  };
  
  const handleCardClick = () => {
    navigate(`/habit/${habit.id}`);
  };
  
  return (
    <CardContainer onClick={handleCardClick}>
      <HabitTitle>{habitTitle}</HabitTitle>
      {habitDescription && <HabitDescription>{habitDescription}</HabitDescription>}
      
      <HabitMeta>
        <HabitCategory>
          {/* Иконка категории здесь */}
          {category}
        </HabitCategory>
        
        <StreakContainer>
          <StreakBadge $completed={isCompletedToday}>
            {current}/{targetStreak}
          </StreakBadge>
        </StreakContainer>
      </HabitMeta>
      
      <ProgressBar>
        <Progress $percentage={cappedPercentage} />
      </ProgressBar>
      
      <ActionButtons>
        <CheckButton 
          $completed={isCompletedToday}
          onClick={handleComplete}
          aria-label="Отметить выполнение"
        >
          {isCompletedToday ? '✓' : '○'}
        </CheckButton>
        
        <ActionButton onClick={handleEdit} aria-label="Редактировать">
          ✎
        </ActionButton>
        
        <ActionButton onClick={handleDelete} aria-label="Удалить">
          ✕
        </ActionButton>
      </ActionButtons>
    </CardContainer>
  );
};

export default HabitCard; 