import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import Button from './ui/Button';
import Input from './ui/Input';
import theme from '../styles/theme';
import { useTranslation } from 'react-i18next';

const FormContainer = styled.form`
  width: 100%;
`;

const FormGroup = styled.div`
  margin-bottom: ${theme.spacing.lg};
`;

const TextArea = styled.textarea`
  width: 100%;
  padding: ${theme.spacing.sm} ${theme.spacing.md};
  font-family: ${theme.typography.fontFamily};
  font-size: ${theme.typography.fontSize.base};
  border: 1px solid ${props => props.$error ? theme.colors.error : theme.colors.gray[300]};
  border-radius: ${theme.borderRadius.md};
  background-color: transparent;
  color: var(--text-color);
  resize: vertical;
  min-height: 100px;
  transition: all ${theme.transitions.fast};
  
  &:focus {
    outline: none;
    border-color: ${theme.colors.primary};
  }
  
  &::placeholder {
    color: ${theme.colors.gray[500]};
  }
`;

const Select = styled.select`
  width: 100%;
  height: 40px;
  padding: 0 ${theme.spacing.md};
  font-family: ${theme.typography.fontFamily};
  font-size: ${theme.typography.fontSize.base};
  border: 1px solid ${props => props.$error ? theme.colors.error : theme.colors.gray[300]};
  border-radius: ${theme.borderRadius.md};
  background-color: transparent;
  color: var(--text-color);
  appearance: none;
  transition: all ${theme.transitions.fast};
  
  &:focus {
    outline: none;
    border-color: ${theme.colors.primary};
  }
  
  &::-ms-expand {
    display: none;
  }
  
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right ${theme.spacing.md} center;
  background-size: 16px;
`;

const ErrorText = styled.div`
  font-size: ${theme.typography.fontSize.small};
  color: ${theme.colors.error};
  margin-top: ${theme.spacing.xs};
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: ${theme.spacing.xl};
  gap: ${theme.spacing.md};
`;

const HabitForm = ({ habit, onSubmit, onCancel, isDisabled = false, initialValues = {} }) => {
  const { t } = useTranslation();
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    frequency: 'daily', // daily, weekly, monthly
    targetStreak: 21, // По умолчанию 21 день для формирования привычки
    category: '',
    startDate: new Date().toISOString().split('T')[0],
    reminderTime: '',
    ...initialValues
  });
  
  const [errors, setErrors] = useState({});
  
  // Если передана привычка для редактирования, заполняем форму ее данными
  useEffect(() => {
    if (habit) {
      setFormData({
        name: habit.name || '',
        description: habit.description || '',
        frequency: habit.frequency || 'daily',
        targetStreak: habit.targetStreak || 21,
        category: habit.category || '',
        startDate: habit.startDate || new Date().toISOString().split('T')[0],
        reminderTime: habit.reminderTime || ''
      });
    }
  }, [habit]);

  // Обработка изменений в полях формы
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
    
    // При изменении полей сбрасываем ошибки
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: null
      }));
    }
  };

  // Валидация формы
  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.name.trim()) {
      newErrors.name = t('habitForm.validation.nameRequired');
    } else if (formData.name.length > 50) {
      newErrors.name = t('habitForm.validation.nameTooLong');
    }
    
    if (formData.description && formData.description.length > 200) {
      newErrors.description = t('habitForm.validation.descriptionTooLong');
    }
    
    const validFrequencies = ['daily', 'weekly', 'monthly'];
    if (!validFrequencies.includes(formData.frequency)) {
      newErrors.frequency = t('habitForm.validation.invalidFrequency');
    }
    
    // Проверка целевой серии
    const targetStreak = parseInt(formData.targetStreak, 10);
    if (isNaN(targetStreak) || targetStreak <= 0) {
      newErrors.targetStreak = t('habitForm.validation.targetStreakInvalid');
    }
    
    // Проверка даты начала
    if (!formData.startDate) {
      newErrors.startDate = t('habitForm.validation.startDateRequired');
    } else {
      const dateRegex = /^\d{4}-\d{2}-\d{2}$/;
      if (!dateRegex.test(formData.startDate)) {
        newErrors.startDate = t('habitForm.validation.startDateInvalidFormat');
      }
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // Обработка отправки формы
  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (validateForm()) {
      onSubmit(formData);
    }
  };

  return (
    <FormContainer onSubmit={handleSubmit}>
      <Input
        label={t('habitForm.nameLabel')}
        id="name"
        name="name"
        type="text"
        value={formData.name}
        onChange={handleChange}
        placeholder={t('habitForm.namePlaceholder')}
        error={errors.name}
        required
        disabled={isDisabled}
      />
      
      <FormGroup>
        <Input
          label={t('habitForm.categoryLabel')}
          id="category"
          name="category"
          type="text"
          value={formData.category}
          onChange={handleChange}
          placeholder={t('habitForm.categoryPlaceholder')}
          disabled={isDisabled}
        />
      </FormGroup>
      
      <FormGroup>
        <label htmlFor="description" style={{ 
          fontSize: theme.typography.fontSize.small,
          color: theme.colors.gray[700],
          marginBottom: theme.spacing.xs,
          display: 'block',
          fontWeight: theme.typography.fontWeight.medium
        }}>
          {t('habitForm.descriptionLabel')}
        </label>
        <TextArea
          id="description"
          name="description"
          value={formData.description}
          onChange={handleChange}
          placeholder={t('habitForm.descriptionPlaceholder')}
          $error={!!errors.description}
          disabled={isDisabled}
        />
        {errors.description && <ErrorText>{errors.description}</ErrorText>}
      </FormGroup>
      
      <FormGroup>
        <label htmlFor="frequency" style={{
          fontSize: theme.typography.fontSize.small,
          color: theme.colors.gray[700],
          marginBottom: theme.spacing.xs,
          display: 'block',
          fontWeight: theme.typography.fontWeight.medium
        }}>
          {t('habitForm.frequencyLabel')}
        </label>
        <Select
          id="frequency"
          name="frequency"
          value={formData.frequency}
          onChange={handleChange}
          $error={!!errors.frequency}
          disabled={isDisabled}
        >
          <option value="daily">{t('habitForm.frequencyDaily')}</option>
          <option value="weekly">{t('habitForm.frequencyWeekly')}</option>
          <option value="monthly">{t('habitForm.frequencyMonthly')}</option>
        </Select>
        {errors.frequency && <ErrorText>{errors.frequency}</ErrorText>}
      </FormGroup>
      
      <FormGroup>
        <Input
          label={t('habitForm.targetStreakLabel')}
          id="targetStreak"
          name="targetStreak"
          type="number"
          min="1"
          value={formData.targetStreak}
          onChange={handleChange}
          error={errors.targetStreak}
          helperText={t('habitForm.targetStreakHelperText')}
          disabled={isDisabled}
        />
      </FormGroup>
      
      <FormGroup>
        <Input
          label={t('habitForm.startDateLabel')}
          id="startDate"
          name="startDate"
          type="date"
          value={formData.startDate}
          onChange={handleChange}
          error={errors.startDate}
          disabled={isDisabled}
        />
      </FormGroup>
      
      <ButtonContainer>
        <Button
          type="button"
          variant="secondary"
          onClick={onCancel}
          disabled={isDisabled}
        >
          {t('habitForm.cancelButton')}
        </Button>
        <Button
          type="submit"
          variant="primary"
          disabled={isDisabled}
        >
          {habit ? t('habitForm.saveButton') : t('habitForm.createButton')}
        </Button>
      </ButtonContainer>
    </FormContainer>
  );
};

export default HabitForm; 