import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import MainLayout from '../components/layout/MainLayout';
import HabitCard from '../components/HabitCard';
import RecommendationsModal from '../components/RecommendationsModal';
import ErrorHandler from '../components/ErrorHandler';
import Button from '../components/ui/Button';
import habitApi from '../services/HabitApi';
import apiService from '../services/ApiService';
import theme from '../styles/theme';
import localforage from 'localforage';

// Стилизованные компоненты
const Content = styled.div`
  padding: ${theme.spacing.md};
  max-width: 800px;
  margin: 0 auto;
  width: 100%;
`;

const EmptyState = styled.div`
  text-align: center;
  padding: ${theme.spacing.xl} 0;
  border: 1px solid ${theme.colors.gray[300]};
  border-radius: ${theme.borderRadius.md};
`;

const EmptyStateTitle = styled.h3`
  margin-bottom: ${theme.spacing.md};
  color: ${theme.colors.gray[800]};
`;

const EmptyStateText = styled.p`
  color: ${theme.colors.gray[600]};
  margin-bottom: ${theme.spacing.lg};
`;

const OfflineBanner = styled.div`
  background-color: ${theme.colors.warning};
  color: white;
  padding: ${theme.spacing.sm} ${theme.spacing.md};
  border-radius: ${theme.borderRadius.md};
  margin-bottom: ${theme.spacing.md};
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const LoadingContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  color: ${theme.colors.gray[600]};
`;

const HomePage = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const location = useLocation();
  const [habits, setHabits] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showRecommendations, setShowRecommendations] = useState(false);
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [isOffline, setIsOffline] = useState(false);
  const [noHabits, setNoHabits] = useState(false);

  useEffect(() => {
    const handleOnlineStatusChange = () => {
      setIsOnline(navigator.onLine);
      if (navigator.onLine) {
        syncData();
      }
    };

    window.addEventListener('online', handleOnlineStatusChange);
    window.addEventListener('offline', handleOnlineStatusChange);

    fetchHabits();

    return () => {
      window.removeEventListener('online', handleOnlineStatusChange);
      window.removeEventListener('offline', handleOnlineStatusChange);
    };
  }, []);

  useEffect(() => {
    fetchHabits();
  }, [location]);

  const syncData = async () => {
    try {
      await apiService.syncLocalChanges();
      await habitApi.syncWithServer();
      
      fetchHabits();
    } catch (error) {
      console.error('Ошибка при синхронизации данных:', error);
    }
  };

  const fetchHabits = async () => {
    try {
      setLoading(true);
      setError(null);
      
      if (!navigator.onLine) {
        setIsOffline(true);
        console.log("Отключен от сети. Загружаю привычки из локального хранилища.");
      } else {
        setIsOffline(false);
        console.log("Онлайн. Пытаюсь загрузить привычки с сервера.");
      }
      
      const fetchedHabits = await habitApi.getHabits();
      console.log("Полученные привычки (fetchHabits):", fetchedHabits);
      
      if (fetchedHabits.length === 0 && !navigator.onLine) {
        setNoHabits(true);
        console.log("Нет привычек в локальном хранилище и нет сети.");
      } else {
        setHabits(fetchedHabits);
        setNoHabits(fetchedHabits.length === 0);
        console.log("Привычки загружены и отфильтрованы.");
      }
      
      setLoading(false);
    } catch (err) {
      console.error('Ошибка при загрузке привычек:', err);
      setError(err);
      setLoading(false);
      
      try {
        const localHabits = await localforage.getItem('habits') || [];
        setHabits(localHabits);
        setNoHabits(localHabits.length === 0);
        setIsOffline(true);
      } catch (localErr) {
        console.error('Не удалось получить локальные данные:', localErr);
        setNoHabits(true);
      }
    }
  };

  const handleComplete = async (id) => {
    try {
      const updatedHabit = await habitApi.completeHabit(id);
      console.log('Ответ сервера:', updatedHabit);
      
      if (!updatedHabit || !updatedHabit.id) {
        throw new Error('Не удалось обновить привычку');
      }
      
      setHabits(prevHabits => {
        const newHabits = prevHabits.map(habit => {
          if (habit.id === id) {
            console.log('Обновляем привычку:', habit.id);
            return updatedHabit;
          }
          return habit;
        });
        return newHabits;
      });
    } catch (err) {
      console.error('Ошибка при отметке привычки как выполненной:', err);
      setError(err);
      // Пробуем обновить данные с сервера
      fetchHabits();
    }
  };

  const handleDelete = async (id) => {
    try {
      await habitApi.deleteHabit(id);
      
      setHabits(prevHabits => 
        prevHabits.filter(habit => habit.id !== id)
      );
    } catch (err) {
      console.error('Ошибка при удалении привычки:', err);
      setError(err);
    }
  };

  const handleEdit = (id) => {
    navigate(`/edit/${id}`);
  };

  // Кнопка действия для заголовка
  const headerActions = (
    <Button 
      onClick={() => navigate('/create')}
      icon="+"
      size="small"
    >
      {t('common.new')}
    </Button>
  );

  return (
    <MainLayout 
      title={t('habits.title')} 
      showBackButton={false}
      headerActions={headerActions}
    >
      <Content>
        {!isOnline && (
          <OfflineBanner>
            <span>{t('common.offlineMessage')}</span>
            <Button 
              variant="text" 
              size="small"
              onClick={() => window.location.reload()} 
            >
              {t('common.refresh')}
            </Button>
          </OfflineBanner>
        )}
        
        {error && <ErrorHandler error={error} onRetry={fetchHabits} />}
        
        {loading ? (
          <LoadingContainer>
            <p>{t('common.loading')}</p>
          </LoadingContainer>
        ) : noHabits ? (
          <EmptyState>
            <EmptyStateTitle>
              {habits.length === 0 
                ? t('habits.emptyState.noHabitsTitle') 
                : t('habits.emptyState.notFoundTitle')}
            </EmptyStateTitle>
            <EmptyStateText>
              {habits.length === 0 
                ? t('habits.emptyState.noHabitsText') 
                : t('habits.emptyState.notFoundText')}
            </EmptyStateText>
            {habits.length === 0 && (
              <Button 
                onClick={() => navigate('/create')}
              >
                {t('habits.add')}
              </Button>
            )}
          </EmptyState>
        ) : (
          habits.map(habit => (
            <HabitCard
              key={habit.id}
              habit={habit}
              onComplete={handleComplete}
              onDelete={handleDelete}
              onEdit={handleEdit}
            />
          ))
        )}
        
        {showRecommendations && (
          <RecommendationsModal
            onClose={() => setShowRecommendations(false)}
            onSelectRecommendation={(rec) => {
              setShowRecommendations(false);
              navigate('/create', { state: { recommendation: rec } });
            }}
          />
        )}
      </Content>
    </MainLayout>
  );
};

export default HomePage; 