import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import styled from 'styled-components';
import MainLayout from '../components/layout/MainLayout';
import HabitForm from '../components/HabitForm';
import ErrorHandler from '../components/ErrorHandler';
import Button from '../components/ui/Button';
import habitApi from '../services/HabitApi';
import theme from '../styles/theme';

const Content = styled.div`
  padding: ${theme.spacing.md};
  max-width: 600px;
  margin: 0 auto;
  width: 100%;
`;

const LoadingContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  color: ${theme.colors.gray[600]};
`;

const LoadingIndicator = styled.div`
  text-align: center;
  padding: ${theme.spacing.md};
  margin-top: ${theme.spacing.lg};
  background-color: rgba(10, 132, 255, 0.1);
  border-radius: ${theme.borderRadius.md};
  color: ${theme.colors.primary};
`;

const ErrorContainer = styled.div`
  text-align: center;
  margin-top: ${theme.spacing.lg};
`;

const EditHabitPage = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const [habit, setHabit] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchHabit();
  }, [id]);

  const fetchHabit = async () => {
    try {
      setIsLoading(true);
      setError(null);
      const data = await habitApi.getHabitById(id);
      
      if (!data) {
        throw new Error('Привычка не найдена');
      }
      
      setHabit(data);
    } catch (err) {
      console.error('Ошибка при загрузке привычки:', err);
      setError(err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = async (habitData) => {
    try {
      setIsLoading(true);
      setError(null);
      await habitApi.updateHabit(id, habitData);
      
      // После успешного обновления перенаправляем на главную страницу
      navigate('/');
    } catch (err) {
      console.error('Ошибка при обновлении привычки:', err);
      setError(err);
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    navigate('/');
  };

  // Кнопка действия для заголовка
  const headerActions = (
    <Button 
      variant="text"
      size="small"
      onClick={handleCancel}
    >
      Отмена
    </Button>
  );

  if (isLoading && !habit) {
    return (
      <MainLayout
        title="Редактирование"
        headerActions={headerActions}
      >
        <Content>
          <LoadingContainer>
            <p>Загрузка данных привычки...</p>
          </LoadingContainer>
        </Content>
      </MainLayout>
    );
  }

  if (error && !habit) {
    return (
      <MainLayout
        title="Редактирование"
        headerActions={headerActions}
      >
        <Content>
          <ErrorHandler 
            error={error} 
            onRetry={fetchHabit}
            customMessage="Не удалось загрузить данные привычки."
          />
          <ErrorContainer>
            <Button
              variant="secondary"
              onClick={() => navigate('/')}
            >
              Вернуться на главную
            </Button>
          </ErrorContainer>
        </Content>
      </MainLayout>
    );
  }

  return (
    <MainLayout
      title={habit ? `Редактирование: ${habit.name}` : 'Редактирование'}
      headerActions={headerActions}
    >
      <Content>
        {error && (
          <ErrorHandler 
            error={error} 
            onRetry={() => setError(null)}
            customMessage="Произошла ошибка при обновлении привычки."
          />
        )}
        
        {habit && (
          <HabitForm 
            habit={habit} 
            onSubmit={handleSubmit} 
            onCancel={handleCancel}
            isDisabled={isLoading}
          />
        )}
        
        {isLoading && habit && (
          <LoadingIndicator>
            Сохранение изменений...
          </LoadingIndicator>
        )}
      </Content>
    </MainLayout>
  );
};

export default EditHabitPage; 