import React from 'react';
import styled, { css } from 'styled-components';
import theme from '../../styles/theme';

const variants = {
  primary: css`
    background-color: ${theme.colors.primary};
    color: white;
    
    &:hover {
      background-color: ${() => {
        // Немного затемняем основной цвет при наведении
        const hex = theme.colors.primary.replace('#', '');
        const r = parseInt(hex.substring(0, 2), 16);
        const g = parseInt(hex.substring(2, 4), 16);
        const b = parseInt(hex.substring(4, 6), 16);
        return `rgba(${r}, ${g}, ${b}, 0.9)`;
      }};
    }
  `,
  secondary: css`
    background-color: ${theme.colors.gray[200]};
    color: ${theme.colors.text.light};
    
    &:hover {
      background-color: ${theme.colors.gray[300]};
    }
  `,
  outline: css`
    background-color: transparent;
    border: 1px solid ${theme.colors.primary};
    color: ${theme.colors.primary};
    
    &:hover {
      background-color: rgba(10, 132, 255, 0.1);
    }
  `,
  text: css`
    background-color: transparent;
    color: ${theme.colors.primary};
    padding: ${theme.spacing.xs} ${theme.spacing.sm};
    
    &:hover {
      background-color: rgba(10, 132, 255, 0.1);
    }
  `,
  danger: css`
    background-color: ${theme.colors.error};
    color: white;
    
    &:hover {
      background-color: ${() => {
        // Немного затемняем красный цвет при наведении
        const hex = theme.colors.error.replace('#', '');
        const r = parseInt(hex.substring(0, 2), 16);
        const g = parseInt(hex.substring(2, 4), 16);
        const b = parseInt(hex.substring(4, 6), 16);
        return `rgba(${r}, ${g}, ${b}, 0.9)`;
      }};
    }
  `,
};

const sizes = {
  small: css`
    font-size: ${theme.typography.fontSize.small};
    padding: ${theme.spacing.xs} ${theme.spacing.sm};
  `,
  medium: css`
    font-size: ${theme.typography.fontSize.base};
    padding: ${theme.spacing.sm} ${theme.spacing.md};
  `,
  large: css`
    font-size: ${theme.typography.fontSize.large};
    padding: ${theme.spacing.sm} ${theme.spacing.lg};
  `,
};

const StyledButton = styled.button`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: ${theme.spacing.xs};
  border-radius: ${theme.borderRadius.md};
  font-weight: ${theme.typography.fontWeight.medium};
  transition: all ${theme.transitions.fast};
  border: none;
  cursor: pointer;
  white-space: nowrap;
  user-select: none;
  
  /* Применяем размер */
  ${({ size }) => sizes[size] || sizes.medium}
  
  /* Применяем вариант */
  ${({ $variant }) => variants[$variant] || variants.primary}
  
  /* Состояние отключенной кнопки */
  ${({ disabled }) =>
    disabled &&
    css`
      opacity: 0.5;
      cursor: not-allowed;
      pointer-events: none;
    `}
  
  /* Полная ширина */
  ${({ $fullWidth }) =>
    $fullWidth &&
    css`
      width: 100%;
    `}
  
  /* Отступы для иконок */
  .button-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    
    &:first-child {
      margin-right: ${({ $iconOnly }) => ($iconOnly ? '0' : theme.spacing.xs)};
    }
    
    &:last-child {
      margin-left: ${({ $iconOnly }) => ($iconOnly ? '0' : theme.spacing.xs)};
    }
  }
  
  /* Кнопка только с иконкой */
  ${({ $iconOnly, size }) =>
    $iconOnly &&
    css`
      padding: ${theme.spacing.xs};
      width: ${() => {
        if (size === 'small') return '32px';
        if (size === 'large') return '48px';
        return '40px';
      }};
      height: ${() => {
        if (size === 'small') return '32px';
        if (size === 'large') return '48px';
        return '40px';
      }};
    `}
`;

const Button = ({
  children,
  variant = 'primary',
  size = 'medium',
  disabled = false,
  fullWidth = false,
  iconOnly = false,
  icon,
  iconPosition = 'left',
  type = 'button',
  onClick,
  ...props
}) => {
  return (
    <StyledButton
      type={type}
      $variant={variant}
      size={size}
      disabled={disabled}
      $fullWidth={fullWidth}
      $iconOnly={iconOnly}
      onClick={disabled ? undefined : onClick}
      {...props}
    >
      {icon && iconPosition === 'left' && <span className="button-icon">{icon}</span>}
      {children && <span>{children}</span>}
      {icon && iconPosition === 'right' && <span className="button-icon">{icon}</span>}
    </StyledButton>
  );
};

export default Button; 