import React from 'react';
import styled from 'styled-components';
import { useLocation, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import theme from '../../styles/theme';

const NavContainer = styled.nav`
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: transparent;
  backdrop-filter: blur(8px);
  border-top: 1px solid ${theme.colors.gray[300]};
  padding: ${theme.spacing.xs} 0;
  z-index: 10;
`;

const NavItem = styled.button`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: ${theme.spacing.xs};
  color: ${props => props.$active ? theme.colors.primary : theme.colors.gray[700]};
  background: transparent;
  border: none;
  font-size: ${theme.typography.fontSize.small};
  font-weight: ${props => props.$active ? theme.typography.fontWeight.medium : theme.typography.fontWeight.regular};
  cursor: pointer;
  flex: 1;
  transition: color ${theme.transitions.fast};
  
  &:hover {
    color: ${theme.colors.primary};
  }
`;

const IconContainer = styled.div`
  font-size: 24px;
  margin-bottom: ${theme.spacing.xxs};
`;

const Label = styled.span`
  font-size: 12px;
`;

// Простые иконки с юникод-символами
const NavIcon = ({ name }) => {
  const icons = {
    home: '◷',
    stats: '📊',
    settings: '⚙️',
    add: '+'
  };

  return <IconContainer>{icons[name] || '◯'}</IconContainer>;
};

const BottomNavigation = () => {
  const { t } = useTranslation();
  const location = useLocation();
  const navigate = useNavigate();

  const navItems = [
    { path: '/', icon: 'home', label: t('navigation.home') },
    { path: '/statistics', icon: 'stats', label: t('navigation.statistics') },
    { path: '/create', icon: 'add', label: t('navigation.add') },
    { path: '/settings', icon: 'settings', label: t('navigation.settings') }
  ];

  return (
    <NavContainer>
      {navItems.map(({ path, icon, label }) => (
        <NavItem
          key={path}
          $active={location.pathname === path}
          onClick={() => navigate(path)}
        >
          <NavIcon name={icon} />
          <Label>{label}</Label>
        </NavItem>
      ))}
    </NavContainer>
  );
};

export default BottomNavigation; 